import React, {Component} from 'react';
import PropTypes from 'prop-types';

let containerStyle = {
    backgroundImage: 'url(https://res.cloudinary.com/dpg94mnti/image/upload/v1705680349/l3kvpjzhzktftos4knn5.jpg)',
    display: 'flex',
    flexDirection: 'column', 
    justifyContent: 'center', 
    alignItems: 'center', 
    backgroundSize: 'cover', 
    backgroundPosition: 'center', 
    width: '800px',
    height: '800px', 
    margin: 'auto'
};

let textStyle = {
    color: 'black',
    fontSize: '18px',
};

let rowStyle = {
    display: 'flex',
    justifyContent: 'center', 
    alignItems: 'center', 
    borderRadius: '50%',
    width: '60px',
    height: '60px',
    textAlign: 'center',
    border: 'solid 2px red'
}


export default class FootComponent extends Component {

    
    render() {
        const {id, value1, value1anomaly, value2, value2anomaly, value3, value3anomaly, value4, value4anomaly, value5, value5anomaly, value6, value6anomaly} = this.props;

        return (
            <div id={id}>
                <div style={containerStyle} >
                    <div style={{alignItems: 'center', marginTop: '110px', display: 'flex'}}>
                    <div style={{ marginRight: '210px', ...rowStyle, backgroundColor: value1anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value1}</span></div>
                        <div style={{...rowStyle, backgroundColor: value4anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value4}</span></div>
                    </div>
                    <div style={{alignItems: 'center', marginTop: '20px', display: 'flex'}}>
                        <div style={{marginRight: '420px', ...rowStyle, backgroundColor: value2anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value2}</span></div>
                        <div style={{...rowStyle, backgroundColor: value5anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value5}</span></div>
                    </div>
                    <div style={{alignItems: 'center', marginTop: '170px', display: 'flex'}}>
                        <div style={{ marginLeft: '20px', marginRight: '102px', ...rowStyle, backgroundColor: value3anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value3}</span></div>
                        <div style={{ marginLeft: '50px', ...rowStyle, backgroundColor: value6anomaly ? '#ff4d4d' : 'lightgrey'}}><span style={textStyle}>{value6}</span></div>
                    </div>
                </div>
            </div>
        );
    }
}

FootComponent.defaultProps = {};

FootComponent.propTypes = {
    id: PropTypes.string,
    label: PropTypes.string.isRequired,
    value1: PropTypes.string,
    value1anomaly: PropTypes.bool,
    value2: PropTypes.string,
    value2anomaly: PropTypes.bool,
    value3: PropTypes.string,
    value3anomaly: PropTypes.bool,
    value4: PropTypes.string,
    value4anomaly: PropTypes.bool,
    value5: PropTypes.string,
    value5anomaly: PropTypes.bool,
    value6: PropTypes.string,
    value6anomaly: PropTypes.bool,
    setProps: PropTypes.func
};
